
// Fonts
@import url('https://fonts.googleapis.com/css2?family=New+Tegomin&family=Noto+Serif+JP:wght@300&display=swap');

// Variables
@import "variables";

@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  font-family: 'Noto Serif JP', serif;
}

body {
  background-color:black;
  color: white;
}

h1,h2,h3,h4,h5,h6 {
  font-family: 'New Tegomin', serif;
}

#main-contents {
  position: relative;
}

#network-canvas {
  position: absolute;
  left: 0;
  top: 0;
  pointer-events: none;
  border: solid 2px #00c300;
  width: 100%;
  height: 100%;
}